
.login-input[data-v-8529e206] {
  width: 90%;
  margin: auto;
  margin-top: 10%;
}
label[data-v-8529e206] {
  position: absolute;
  left: 10px;
  font-size: 18px;
  -webkit-transform: translateY(6px);
          transform: translateY(6px);
  transition: all 0.25s ease;
  -webkit-backface-visibility: hidden;
  pointer-events: none;
}

/*COLOR*/
.font-white label[data-v-8529e206]{
  color: rgba(255, 255, 255, 0.4);
}
.font-black label[data-v-8529e206]{
  color: rgba(0, 0, 0, 0.7);
}
label .req[data-v-8529e206] {
  margin: 2px;
  color: #009688;
}

/*REQ COLOR*/
.line-white label .req[data-v-8529e206] {
  color: #ffffff;
}
.line-black label .req[data-v-8529e206] {
  color: #000000;
}
.line-teal label .req[data-v-8529e206] {
  color: #009688;
}
label.active[data-v-8529e206] {
  -webkit-transform: translateY(-15px);
          transform: translateY(-15px);
  left: 2px;
  font-size: 14px;
}
label.active .req[data-v-8529e206] {
  opacity: 0;
}
.font-white label.highlight[data-v-8529e206] {
  color: #ffffff;
}
.font-black label.highlight[data-v-8529e206] {
  color: #000000;
}
input[data-v-8529e206], textarea[data-v-8529e206] {
  font-size: 22px;
  display: block;
  width: 96%;
  height: 100%;
  padding: 7px 7px;
  background: none;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom: 1px solid;
  /*border-color: rgba(255, 255, 255, 0.4);
  color: #ffffff;*/
  border-radius: 0;
  transition: border-color .25s ease, box-shadow .25s ease;
}
input[data-v-8529e206]:focus {
  outline: 0;
  /*border-color: #1ab188;*/
}

/* INPUT COLOR */
.font-white input[data-v-8529e206], textarea[data-v-8529e206]{
  border-color: rgba(255, 255, 255, 0.4);
  color: #ffffff;
}
.font-black input[data-v-8529e206], textarea[data-v-8529e206]{
  border-color: rgba(0, 0, 0, 0.7);
  color: #000000;
}
.line-white input[data-v-8529e206]:focus{
  border-color: #ffffff;
}
.line-black input[data-v-8529e206]:focus{
  border-color: #000000;
}
.line-teal input[data-v-8529e206]:focus{
